import React, { Component } from 'react';

class Resume extends Component {
	getRandomColor() {
		var letters = '0123456789ABCDEF';
		var color = '#';
		for (var i = 0; i < 6; i++) {
			color += letters[Math.floor(Math.random() * 16)];
		}
		return color;
	}

	render() {
		if (this.props.data) {
			var skillmessage = this.props.data.skillmessage;
			var education = this.props.data.education.map(function (education) {
				return (
					<div key={education.school}>
						<h3>{education.school}</h3>
						<p className='info'>
							{education.degree} <span>&bull;</span>
							<em className='date'>{education.graduated}</em>
						</p>
						<p>{education.description}</p>
					</div>
				);
			});
			var work = this.props.data.work.map(function (work) {
				return (
					<div key={work.company}>
						<h3>{work.company}</h3>
						<p className='info'>
							{work.title}
							<span>&bull;</span>{' '}
							<em className='date'>{work.years}</em>
						</p>
						<p>{work.description}</p>
					</div>
				);
			});
			var volunteer = this.props.data.volunteer.map(function (volunteer) {
				return (
					<div key={volunteer.name}>
						<h3>{volunteer.name}</h3>
						<p className='info'>
							{volunteer.title}
							<span>&bull;</span>{' '}
							<em className='date'>{volunteer.years}</em>
						</p>
						<p>{volunteer.description}</p>
					</div>
				);
			});
			var skills = this.props.data.skills.map((skills) => {
				var className = 'bar-expand ' + skills.name.toLowerCase();
				return (
					<li key={skills.name}>
						<span
							style={{
								width: skills.level,
								backgroundColor: this.getRandomColor(),
							}}
							className={className}></span>
						<em>{skills.name}</em>
					</li>
				);
			});
		}

		return (
			<section id='resume'>
				<div className='row education'>
					<div className='three columns header-col'>
						<h1>
							<span>Education</span>
						</h1>
					</div>

					<div className='nine columns main-col'>
						<div className='row item'>
							<div className='twelve columns'>{education}</div>
						</div>
					</div>
				</div>

				<div className='row work'>
					<div className='three columns header-col'>
						<h1>
							<span>Work</span>
						</h1>
					</div>
					<div className='nine columns main-col'>
						<div className='row item'>
							<div className='twelve columns'>{work}</div>
						</div>
					</div>
				</div>

				<div className='row volunteer'>
					<div className='three columns header-col'>
						<h1>
							<span>Volunteer</span>
						</h1>
					</div>
					<div className='nine columns main-col'>{volunteer}</div>
				</div>

				<div className='row skill'>
					<div className='three columns header-col'>
						<h1>
							<span>Skills</span>
						</h1>
					</div>

					<div className='nine columns main-col'>
						<b>
							<p>{skillmessage}</p>
						</b>

						<div className='bars'>
							<ul className='skills'>{skills}</ul>
						</div>
					</div>
				</div>
			</section>
		);
	}
}

export default Resume;
